import languages from '@cospired/i18n-iso-languages';
import i18n from '../i18n';

export const FORMAT_PHONE_NO = 'xxx-xxx-xxxx';
export const FORMAT_ZIP_CODE = 'xxxxx';
export const FORMAT_DATE_OF_BIRTH = 'MM/DD/YYYY';

const supportedCodes = Object.keys(i18n.options.resources || {});

export const logger = (...args: any[]) => {
  if (__DEV__) {
    console.log(...args);
  }
};

// Hàm import động locale tương ứng
supportedCodes.forEach(code => {
  try {
    const langData = require(`@cospired/i18n-iso-languages/langs/${code}.json`);
    languages.registerLocale(langData);
  } catch {
    // fallback nếu locale không tồn tại trong thư viện
    console.warn(`No locale found for: ${code}`);
  }
});

export const getAvailableLanguages = () => {
  const current = i18n.language || 'en';
  return supportedCodes.map(code => ({
    key: code,
    value:
      languages.getName(code, current) ||
      languages.getName(code, 'en') ||
      code.toUpperCase(),
  }));
};

export const generateUUID = (): string => {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    // eslint-disable-next-line no-bitwise
    const r = (Math.random() * 16) | 0;
    // eslint-disable-next-line no-bitwise
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
};

export const formatPhoneNumber = (input: string): string => {
  const digits = input.replace(/\D/g, '');

  if (digits.length <= 3) return digits;
  if (digits.length <= 6) return `${digits.slice(0, 3)}-${digits.slice(3)}`;

  return `${digits.slice(0, 3)}-${digits.slice(3, 6)}-${digits.slice(6, 10)}`;
};

export const formatMiles = (distance: number) => {
  const formatted = Number(distance).toFixed(1);
  return formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted;
};

export const formatCurrency = (value: number): string => {
  const units = ['', 'K', 'M', 'B', 'T', 'Qa', 'Qi']; // K: 10^3, M: 10^6, ..., Qi: 10^18
  let unitIndex = 0;

  if (value < 1000000000000) {
    return (
      '$' +
      new Intl.NumberFormat('en-US', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
        useGrouping: true,
      }).format(Math.floor(value * 100) / 100)
    ); // Cắt bỏ phần thừa
  }

  while (value >= 1000 && unitIndex < units.length - 1) {
    value /= 1000;
    unitIndex++;
  }

  // Cắt phần lẻ mà không làm tròn lên
  const formattedValue = Math.floor(value * 100) / 100;

  return (
    '$' +
    formattedValue.toString().replace(/\.?0+$/, '') +
    ' ' +
    units[unitIndex]
  );
};
